/* add css module styles here (optional) */

._1Ths4 {
  margin: 0;
  padding: 0;
}

._1BM4R {
  padding: 0;
  max-width: 100%;
  overflow: hidden;
  height: 100%;
  min-height: 500px;
  position: relative;
  background-color: #2c3051;
}

._e9mUc {
  perspective: 800px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

._1EccV {
  cursor: crosshair;
  box-sizing: content-box;
  position: relative;
  width: auto;
  height: auto;
  transform: rotateX(0deg) scale(1);
  transform-style: preserve-3d;
  border: 100px solid #2c3051;
  background-color: #f1f1f1;
}

._3LMIu,
._2Bdmc {
  transition: opacity 0.3s ease-in-out;
  z-index: 10;
  box-sizing: content-box;
  pointer-events: none;
  position: absolute;
  background-color: #cfcfcf;
  opacity: 0;
}

._3LMIu._3YCtJ,
._2Bdmc._3WW1y {
  opacity: 1;
}

._3LMIu::before {
  content: '';
  position: absolute;
  width: 2px;
  height: 10px;
  top: -4px;
  left: var(--x-position);
  background-color: rgba(255, 71, 0, 1);
}
._3LMIu::after {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 800;
  content: attr(data-x-position);
  position: absolute;
  top: -25px;
  left: var(--x-position);
  text-align: right;
  padding-left: 2px;
}

._2Bdmc::before {
  content: '';
  position: absolute;
  width: 10px;
  height: 2px;
  left: -4px;
  top: var(--y-position);
  background-color: rgba(255, 71, 0, 1);
}
._2Bdmc::after {
  color: #FFFFFF;
  font-size: 18px;
  font-weight: 800;
  content: attr(data-y-position);
  position: absolute;
  left: -88px;
  top: var(--y-position);
  text-align: right;
  width: 83px;
}